import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import * as firebase from 'firebase';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Account } from 'src/Table/table';
@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private usersCollectionList: AngularFirestoreCollection<any>;
  private usersList: Observable<Account[]>;
  constructor(private db: AngularFirestore) {
    this.usersCollectionList = db.collection<Account>('Account');
  }

  // loginUser(email: string,password: StringConstructor): Promise<firebase.auth.UserCredential> {
  //   return firebase.auth().signInWithEmailAndPassword(email, password);
  // }
  // signUpUser( email: string,password: string): Promise<any> {
  //   return firebase.auth().createUserWithEmailAndPassword(email, password);
  // }
  signUpUser(email: string, password: string) {
    const usersObj = this.db.collection('Account', ref => ref.where('email', '==', email)
      .where('password', '==', password)).snapshotChanges();
    this.usersList = usersObj.pipe(
      map(changes => changes.map(a => {
        const data = a.payload.doc.data() as Account;
        const id = a.payload.doc.id;
        return { id, ...data };
      }))
    );
    return this.usersList;
  }

  resetPassword(email: string): Promise<void> {
    return firebase.auth().sendPasswordResetEmail(email);
  }
  logOutUser(): Promise<void> {
    return firebase.auth().signOut();
  }


}
