import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  },
  {
    path: '',
    loadChildren: () => import("./menu/menu.module").then( m => m.MenuPageModule)
  },
  {
    path: 'login',
    loadChildren: () => import('./login/login.module').then( m => m.LoginPageModule)
  },
  {
    path: 'location',
    loadChildren: () => import('./location/location.module').then( m => m.LocationPageModule)
  },
  {
    path: 'signup',
    loadChildren: () => import('./signup/signup.module').then( m => m.SignupPageModule)
  },
  {
    path: 'user',
    loadChildren: () => import('./user/user.module').then( m => m.UserPageModule)
  },
  {
    path: 'driver-history',
    loadChildren: () => import('./driver-history/driver-history.module').then( m => m.DriverHistoryPageModule)
  },
  {
    path: 'signup-customer',
    loadChildren: () => import('./signup-customer/signup-customer.module').then( m => m.SignupCustomerPageModule)
  },
  {
    path: 'restaurant-history',
    loadChildren: () => import('./restaurant-history/restaurant-history.module').then( m => m.RestaurantHistoryPageModule)
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
      ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
